import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { getListParameter } from "../../actions/parameterTransaksiAction";
import { getrekeningList } from "../../actions/rekeningAction";
import { renderField } from "../../renderField";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormKelolaUangBank extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listKategori: [],
      listRekening: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListParameter());
    this.props.dispatch(getrekeningList());
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 col-lg-4">
            <Field
              name="tanggal"
              label="Tanggal"
              type="date"
              component={renderField}
            />
          </div>
          {this.props.from === "bank" ? (
            <div className="col-md-4 col-lg-4">
              <label htmlFor="">Rekening</label>
              <Field
                name="rekening"
                className="form-control"
                component="select"
              >
                <option value="DEFAULT">SILAHKAN PILIH</option>
                {this.props.rekening.map((list) => (
                  <option value={list.no_rekening} key={list.no_rekening}>
                    {list.atas_nama}
                  </option>
                ))}
              </Field>
            </div>
          ) : (
            <div className="col-md-4 col-lg-4"></div>
          )}
          <div className="col-md-4 col-lg-4"></div>
          <div className="col-lg-4 col-md-4">
            <label htmlFor="">Jumlah</label>
            <Field
              name="jumlah"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Kategori</label>
            <Field name="kategori" className="form-control" component="select">
              <option value="DEFAULT">SILAHKAN PILIH</option>
              {this.props.parameter.map((list) => (
                <option
                  value={list.kategori_transaksi}
                  key={list.kategori_transaksi}
                >
                  {list.kategori_transaksi}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="keperluan"
              label="Untuk Keperluan"
              type="text"
              component={renderField}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Simpan
                <i className="fa fa-paper-plane ml-3"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKelolaUangBank = reduxForm({
  form: "FormKelolaUangBank",
  enableReinitialize: true,
})(FormKelolaUangBank);
export default connect((state) => {
  return {
    parameter: state.provinsi.parameterTransaksi,
    rekening: state.provinsi.rekening,
  };
})(FormKelolaUangBank);
